<script>
import { mapActions, mapGetters } from "vuex";
import { Debounce } from "@/utils/public";
import {
  getData,
  opthionList,
  repertory,
  getCustomerList
} from "common/utils/public_function/commonUtils";
export default {
  name: "aftersaleThreeinIndex",
  data() {
    return {
      add: {
        tire_json: []
      },
      paramsChange: {}, //修改
      GoodsTire: {}, //查看胎号
      dialogFormVisibleChange: false,
      dialogFormVisibleSee: false,
      companyList: [],
      companys: {
        options: []
      },
      goodsList: [], //商品下拉
      SelesTomer: {
        options: []
      }, //客户列表
      params: {
        auditor_status: 0
      },
      searchParams: {},
      dialogFormVisible: false
    };
  },
  computed: {
    ...mapGetters(["threeinIndex_Get"])
  },
  methods: {
    ...mapActions(["threeinIndex", "threeinExamine", "threeinAdd"]),
    change(e) {
      this.$forceUpdate();
    },
    departmentChange(val) {
      // this.$set(this.params, "warehouse_id", "");
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    // 检测胎号
    checkTire: Debounce(function(row) {
      if (row.tire_un) {
        this.$axios({
          method: "get",
          url: "v2/open/Tire/getTireMatchStatus",
          params: {
            type: 7,
            goods_id: this.add.goods_id,
            tire_un: row.tire_un,
            company_id: this.add.company_id
          }
        }).then(({ data }) => {
          if (data.code === 200 && data.data && data.data.tire_id) {
            row.check_status = "匹配";
          } else {
            row.check_status = "不匹配";
          }
          console.log(data);
        });
      }
    }, 500),
    changeAll(val) {
      this.$set(this.paramsChange, "allocation_id", "");
    },
    goSaleputSee(row) {
      this.$router.push({
        path: "/sales/salesout/salesamend",
        query: {
          id: row.outstock_id,
          printSstatus: row.after_auditor_print_status
        }
      });
    },
    clearAdd() {
      // 弹框取消
      this.$set(this, "add", {
        tire_json: []
      });
      this.dialogFormVisible = false;
    },
    submitChange() {
      //修改确定方法
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          company_id: this.paramsChange.company_id,
          company_name: this.paramsChange.company_name,
          bill_type: 8,
          bill_id: this.paramsChange.instorage_id,
          operation_type: 2,
          goods_info: JSON.stringify([
            {
              warehouse_id: this.paramsChange.warehouse_id,
              warehouse_name: this.paramsChange.warehouse_name
            }
          ])
        })
        .then(() => {
          this.$axMethod.editInStorage(this.paramsChange).then(response => {
            if (response.data.code == 200) {
              this.$message({
                message: response.data.msg
                  ? response.data.msg
                  : response.data.data,
                type: "success"
              });
            }
            this.dialogFormVisibleChange = false;
            getData(this.searchParams);
          });
        });
    },
    Sure() {
      // 弹框确认取消
      for (var i = 0; i < this.add.tire_json.length; i++) {
        if (!this.add.tire_json[i].tire_un) {
          this.$message({
            message: "胎号需全部录入",
            type: "warning"
          });
          return false;
        }
      }
      let json_infos = {
        instorage_goods_id: this.add.instorage_goods_id,
        goods_id: this.add.goods_id,
        goods_sn: this.add.goods_sn,
        tire_json: JSON.stringify(this.add.tire_json)
      };

      this.$axMethod.addGoodsTire(json_infos).then(response => {
        if (response.data.code == 200) {
          this.$message({
            message: response.data.msg ? response.data.msg : response.data.data,
            type: "success"
          });
          getData(this.searchParams);
        }
      });
      this.dialogFormVisible = false;
      getData(this.searchParams);
    },
    untilDetail(scope, row) {
      //胎号录入
      this.$axMethod
        .getGoodsTire({
          instorage_goods_id: row.instorage_goods_id
        })
        .then(res => {
          var result = res.data.data;
          result.instorage_goods_id = row.instorage_goods_id;
          result.tire_json = [];
          for (const obj of result.tire_list) {
            result.tire_json.push({
              tire_un: obj.tire_un,
              check_status: obj.check_status,
            });
          }
          if (!result.tire_json.length)
            for (var x = 1; x <= row.goods_num; x++) {
              result.tire_json.push({
                tire_un: "",
                check_status: '',
              });
            }
          this.add = result;
          this.dialogFormVisible = true;
        });
    },
    seeDetail(row) {
      //胎号查看
      this.$axMethod
        .getGoodsTire({
          instorage_goods_id: row.instorage_goods_id
        })
        .then(res => {
          this.GoodsTire = res.data.data;
        });
      this.dialogFormVisibleSee = true;
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "warehouse_id", ""); // 初始化仓库下拉选中值
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
      this.$set(this.params, "allocation_id", ""); // 初始化货位下拉选中值
      // getDepartmentListCommon(this, this.params, 1);
    },
    customerListChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    allocationChange(val) {
      if (val) {
        this.params.allocation_name = this.allocationInventoryList.filter(
          obj => obj.allocation_id == val
        )[0].allocation_name;
      }
    },
    allocation(val) {
      this.$set(this.params, "allocation_id", ""); // 初始化货位下拉选中值
      // this.$axMethod
      //   .allocationInventoryList({
      //     warehouse_id: val
      //   })
      //   .then(({ data }) => {
      //     if (data.code == "200") {
      //       this.allocationInventoryList = data.data;
      //     } //货位列表下拉
      //   });
      if (val) {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
    },
    handleEdit(scope, row) {
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      // //修改三包入库显示与隐藏的方法
      // this.threeinExamine({
      //   moduleName: "companyExamine",
      //   instorage_goods_id: row.instorage_goods_id
      // });
      this.dialogFormVisibleChange = true;
      this.$axMethod
        .getInStorageInfo({
          instorage_goods_id: row.instorage_goods_id,
          goPage: this.goPage,
          pageSize: this.pageSize
        })
        .then(response => {
          this.paramsChange = response.data.data;
          // bindIdsF(this, response.data.data, [
          //   ["warehouse_id", "warehouse_name", "warehouseList", "options"],
          //   [
          //     "allocation_id",
          //     "allocation_name",
          //     "allocationInventoryList",
          //     "options"
          //   ]
          // ]);
          opthionList(this, this.paramsChange);
        });
    },
    // getWarehouseFoucs() {
    //   this.$common.options("getWarehouseListOption", this.warehouseList, {
    //     company_id: localStorage.getItem("company_id")
    //   }); //仓库下拉
    // },
    // getAllocationInventoryFoucs() {
    //   this.$axMethod
    //     .allocationInventoryList({
    //       warehouse_id: this.paramsChange.warehouse_id
    //     })
    //     .then(({ data }) => {
    //       if (data.code == "200") {
    //         this.allocationInventoryList.options = data.data;
    //       } //货位列表下拉
    //     });
    // },
    examineFun() {
      //审核
      let vm = this;
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      var car = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_goods_id);
        car.push(this.multipleSelection[i].instorage_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "instorage_goods_id",
        eachData: {
          instorage_goods_id: rebateArr
        },
        examineApi: "auditorPass",
        list: {
          UrlApi: "getInStorageList",
          data: this.TableData,
          esData: {}
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 8,
          bill_id: car.join(","),
          operation_type: 4
        })
        .then(() => {
          vm.$common.examineFun(funcan);
        });
    },
    examineFunF() {
      //弃审
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      var car = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_goods_id);
        car.push(this.multipleSelection[i].instorage_id);
      }
      var rebateArr = addr.toString();
      let obj = {
        instorage_goods_id: rebateArr
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 8,
          bill_id: car.join(","),
          operation_type: 5
        })
        .then(() => {
          this.$axMethod.returnAuditorPass(obj).then(response => {
            if (response.data.code == 200) {
              this.$message({
                message: response.data.data,
                type: "success"
              });
              this.$common.initData("getInStorageList", this.TableData);
            }
          });
        });
    },
    abolishFun() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      var car = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_goods_id);
        car.push(this.multipleSelection[i].instorage_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "instorage_goods_id",
        eachData: {
          instorage_goods_id: rebateArr
        },
        examineApi: "auditorInvalid",
        list: {
          UrlApi: "getInStorageList",
          data: this.TableData,
          esData: {}
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 8,
          bill_id: car.join(","),
          operation_type: 6
        })
        .then(() => {
          this.$common.examineFun(funcan);
        });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    salesAddF() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      }
      this.$router.push({
        path: "/aftersale/threein/threein_Add"
      });
      this.threeinAdd({
        // moduleName: "companyAdd",
        goPage: this.goPage,
        pageSize: this.pageSize,
        params: this.params
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getInStorageList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
      // wz cache
      this.threeinIndex({
        params: {},
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: this.goPage
      });
      getData(this.searchParams);
      // wz cache
      this.threeinIndex({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要删除的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      var car = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_goods_id);
        car.push(this.multipleSelection[i].instorage_id);
      }
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 8,
          bill_id: car.join(","),
          operation_type: 3
        })
        .then(() => {
          var deledata = {
            deleID: "instorage_goods_id",
            deleSelect: this.multipleSelection,
            deleApi: "delInStorage",
            list: {
              UrlApi: "getInStorageList",
              data: this.TableData,
              esData: Object.assign({}, this.params)
            }
          };
          this.$common.deletes(deledata, "删除", this);
        });
    }
  },
  mounted() {
    // wz cache
    let wz_search_params = {};
    if (this.threeinIndex_Get && this.threeinIndex_Get.params) {
      if (this.threeinIndex_Get.params.start_time) {
        const { start_time, end_time } = this.threeinIndex_Get.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      this.$set(this, "params", this.threeinIndex_Get.params);
      wz_search_params = this.params;
      opthionList(this, this.threeinIndex_Get.params);
    }
    if (this.threeinIndex_Get && this.threeinIndex_Get.goPage) {
      this.params.page = this.threeinIndex_Get.goPage;
      this.params.per_page = this.threeinIndex_Get.pageSize;
    }
    this.$set(this, "searchParams", {
      params: wz_search_params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getInStorageList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams); //获取销售订单列表
  }
};
</script>

